.field {
    /* width: fit-content; */
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 30px 0;
    /* position: relative; */
}

.blockChoicePLayer {
    display: flex;
    padding: 10px;
}

.blockChoicePLayer_item {
    margin-right: 10px;
    cursor: pointer;
}

.blockChoicePLayer_item:hover {
    color: red
}

.field_fight {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: fit-content;
    /* transform: rotate(90deg); */
}

.row {
    display: flex;
}

.col {}

.cell {
    /* border: 3px solid black; */
    cursor: pointer;

}

.cell_active {
    width: 40px;
    height: 45px;
    background: rgba(0, 0, 255, 0.352);
    border-radius: 15px;
    z-index: 8;
    position: absolute;
    top: 10%;
    left: 10%;
}

.cell_disActive {
    width: 40px;
    height: 45px;
    background: rgba(255, 0, 0, 0.19);
    border-radius: 15px;
    
    position: absolute;
    top: 4%;
    left: 10%;
}

.col img {
    width: 50px;
    height: 50px;
    align-items: center;
    /* transform: rotate(30deg) ; */
}

.row img {
    width: 50px;
    height: 50px;
    align-items: center;
    /* transform: rotate(30deg) ; */
}

.list__tanks{
    position: absolute;
    top: 30%;
    left: 80%;
}


.activeFire{
    position: absolute;
    width: 40px;
    height: 40px;
    transform: translate(-5px, -5px);
    /* top: 0;
    left: 0; */
    /* right: 0;
    bottom: 0; */
    background: #000;
    z-index: 5;
}